/*-------- Feedback --------*/

.feedback {
    background-color: #1d4693;

    &__wrapper {
        display: flex;
        align-items: center;
        justify-content: space-between;
        height: 270px;
    }

    &__buttons {
        display: flex;
    }

    &__title {
        font-family: $font-pf;
        font-size: 58px;
        color: #ffffff;
    }

    &__button {
        height: 52px;
    }

    &__button:not(:first-child) {
        margin-left: 25px;
    }
}