/*-------- Service --------*/

.service {
    padding-top: 129px;

    &__banner {
        color: #ffffff;
        background-color: #1d4693;
    }

    &__banner-columns {
        display: flex;    
        align-items: center;
        padding-top: 70px;
        padding-bottom: 70px;
    }

    &__banner-column--1 {
        width: 55%;
    }

    &__banner-column--2 {
        width: 45%;
        padding-left: 56px;
    }

    &__banner-title {
        font-family: $font-pf;
        font-size: 58px;
    }

    &__line-text {
        align-items: flex-start;
        margin-top: 50px;
    }

    &__tags {
        display: flex;
        align-items: center;
        margin-top: 85px;
    }

    &__tag:not(:first-child) {
        margin-left: 30px;
    }

    &__tag-image {
        width: 100%;
    }

    &__form {
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;

        padding: 50px 36px;
        box-shadow: 1px 2px 9px 1px rgba(0, 1, 1, 0.22);

        background-color: #16336b;
        border-radius: 4px;
    }

    &__input-wrapper {
        width: 100%;
    }

    &__input-wrapper--50 {
        width: 49%;
    }

    &__input-wrapper:nth-child(n+3) {
        margin-top: 21px;
    }

    &__form-button {
        margin-top: 40px;
        margin-left: auto;
        margin-right: auto;
    }

    &__services {
        padding-top: 117px;
        padding-bottom: 157px;
        position: relative;
    }

    &__services-wrapper {
        position: relative;
        z-index: 2;
    }

    &__services-columns {
        display: flex;
        align-items: center;
    }

    &__services-column--1 {
        width: 46%;
    }

    &__services-column--2 {
        width: 54%;
        padding-left: 49px;
        z-index: 1;
    }

    &__services-text {
        font-size: 19px;
        line-height: 1.6;
    }

    &__services-image {
        width: 100%;
    }

    &__services-buttons {
        display: flex;
        justify-content: center; 
        margin-top: 40px;
    }

    &__services-button {
        height: 52px;
        padding: 0 36px;
    }

    &__services-button:not(:first-child) {
        margin-left: 20px;
    }

    &__background {
        width: 100%;
        position: absolute;
        left: 0;
        bottom: 0;
    }

    &__cards-wrapper {
        margin-top: 150px;
        z-index: 2;
        position: relative;
    }

    &__services-cards {
        display: flex;
        flex-wrap: wrap;
        margin-top: 123px;
    }

    &__services-card {
        width: calc(33% - 16px);
        padding: 45px 40px;
        background-color: #ffffff;
        box-shadow: 1px 2px 9px 1px rgba(0, 1, 1, 0.22);
        border-radius: 5px;
        transition: background-color 0.2s ease-in-out;
    }

    &__services-card:hover {
        background-color: #1d4693;
        color: #ffffff;
    }

    &__services-card:not(:nth-child(3n+1)) {
        margin-left: 30px;
    }

    &__services-card:nth-child(n+4) {
        margin-top: 30px;
    }

    &__services-card-title {
        min-height: 70px;
        margin-top: 35px;

        font-family: $font-sb;
        font-size: 27px;
        line-height: 1.3;
        transition: color 0.2s ease-in-out;
    }

    &__services-card-text {
        margin-top: 30px;
        font-size: 19px;
        line-height: 1.5;
        transition: color 0.2s ease-in-out;
    }

    &__content {
        position: relative;
    }

    &__content-wrapper {
        padding-top: 80px;
        z-index: 1;
        position: relative;
    }

    &__content-image {
        float: right;
        padding-left: 50px;
    }

    &__content-title {
        font-family: $font-pf;
        font-size: 64px;
    }

    &__content-subtitle {
        margin-top: 70px;
        font-family: $font-m;
        font-size: 22px;
        color: #1d4693;
    }

    &__content-text {
        margin-top: 60px;
        font-size: 19px;
        line-height: 1.5;
    }

    &__content-text--strong {
        display: block;
        font-family: $font-sb;
        font-size: 27px;
    }
}

@media (max-width: 1099px) {

    .service {
        padding-top: 90px;

        &__banner-columns {
            padding-top: 40px;
            padding-bottom: 40px;
        }

        &__tag:not(:first-child) {
            margin-left: 20px;
        }

        &__tags {
            margin-top: 45px;
        }

        &__line-text {
            margin-top: 40px;
            font-size: 24px;
        }
        
        &__banner-title {
            font-size: 45px;
        }

        &__banner-column--2 {
            padding-left: 30px;
        }
    }
}