/*-------- Input-wrapper --------*/

.input-wrapper {
    
    &__label {
        display: block;
        margin-bottom: 22px;
        font-family: $font-b;
        font-size: 15px;
    }

    &__label--white {
        color: #ffffff;
    }

    &__input {
        width: 100%;
    }
}